import { Injectable } from '@angular/core';
import { TokenProviderModel } from '../../models/token-payload.model';
import { ITokenProvider } from '../../interfaces/token-payload-provider.interface';
@Injectable({
  providedIn: 'root'
})
export class SessionstorageTokenProviderService<userDetailsType,userRolesType> implements ITokenProvider<userDetailsType,userRolesType>{
  private userSessionKey = 'token';

  public removeToken():void {
    sessionStorage.removeItem(this.userSessionKey);
  }

  public setToken(token: TokenProviderModel<userDetailsType,userRolesType>): void {
    sessionStorage.setItem(this.userSessionKey, JSON.stringify(token));
  }

  public getToken(): TokenProviderModel<userDetailsType,userRolesType> {
      if(sessionStorage.getItem(this.userSessionKey)){
        return JSON.parse(sessionStorage.getItem(this.userSessionKey)as string) as TokenProviderModel<userDetailsType,userRolesType>; 
      }
      return undefined!;
  }
}
