import { forwardRef, Injectable } from "@angular/core";
import { TokenProviderModel } from "../models/token-payload.model";
import { SessionstorageTokenProviderService } from "../services/implements/sessionstorage-token-provider.service";

export interface ITokenProvider <UserDetailsType, UserRolesType> {
    getToken():  TokenProviderModel<UserDetailsType, UserRolesType>;
    setToken(token: TokenProviderModel<UserDetailsType,UserRolesType>);
    removeToken():void;
}

export abstract class TokenPorviderService <UserDetailsType = any, UserRolesType = any> implements ITokenProvider <UserDetailsType, UserRolesType>{
    public abstract getToken(): TokenProviderModel<UserDetailsType, UserRolesType>
    public abstract setToken(token: TokenProviderModel<UserDetailsType, UserRolesType>):void
    public abstract removeToken():void
}