import { Component, Inject, OnInit, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthenticationService } from '../services/auth.service';
import { CONFIGLIBRARY, IConfigLibrary } from '../../../config-library.interface';
import Swal from 'sweetalert2';

@Component({
  selector: 'lib-security-library-recovery-password',
  templateUrl: './recovery-password.component.html',
  styleUrls: ['./recovery-password.component.scss']
})
export class RecoveryPasswordComponent implements OnInit {
  @Output() returnToLogin = new EventEmitter();
  @Output() restorePass = new EventEmitter();
  public recoveryForm!: FormGroup;

  constructor(
    @Inject(CONFIGLIBRARY) configLibrary: IConfigLibrary,
    private fb: FormBuilder,
    private authService: AuthenticationService,
  ) { }


  ngOnInit(): void {
    this.loadForm();
  }

  private loadForm() {
    this.recoveryForm = this.fb.group({
      email: new FormControl(null, [Validators.required, Validators.maxLength(50)])
    })
  }

  backtoLogin() {
    this.returnToLogin.emit(true);
  }

  restablecerPass() {
    if (this.recoveryForm.invalid) {
      this.restorePass.emit(false)
    }
    else {
      this.authResetPass();
    }
  }

  private authResetPass(){
    this.authService.resetPassword(this.recoveryForm.value).subscribe(
      response => {
          this.restorePass.emit(true);
      },
      error => {
         console.log(error)
         this.restorePass.emit(false)
      }
    )
  }

}
