import { Inject, Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpHeaders, HttpResponse } from '@angular/common/http';

import { BehaviorSubject, Observable, throwError } from 'rxjs';
import { catchError, filter, retryWhen, switchMap, take, tap } from 'rxjs/operators';
import Swal from 'sweetalert2';

import { Router } from '@angular/router';

import { CONFIGLIBRARY, IConfigLibrary, IWhiteList } from "../../../config-library.interface";

import { AuthenticationService } from '../services/auth.service';

import { TokenProviderModel } from "../models/token-payload.model";
import { TokenPorviderService } from '../interfaces/token-payload-provider.interface';
import { ResponseApiDTO } from '../interfaces/response-api.interface';
import { EncrDecrService } from '../services/encrDecr.service';
import { TokenPayloadDTO } from '../interfaces/token-payload-interface';
import { AuthMapperService } from '../services/authMapper.service';

@Injectable()
export class AuthInterceptor implements HttpInterceptor {
  token!: TokenProviderModel<any, any>;
  whiteList: Array<IWhiteList>
  refreshTokenURL: string;
  accessTokenURL: string;
  
  constructor(
    @Inject(CONFIGLIBRARY) configLibrary: IConfigLibrary,
    private router: Router,
    private tokenPorviderService: TokenPorviderService, 
    private authService: AuthenticationService,
    private auth: AuthenticationService<any, any>,
    private encrDecr: EncrDecrService,
    private _authMapperService: AuthMapperService
  ){
    this.whiteList = configLibrary.whiteList
    this.refreshTokenURL = configLibrary.refreshTokenURL;
    this.accessTokenURL = configLibrary.accessTokenURL;
  }

  intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    this.token = this.tokenPorviderService.getToken();
    let isNeedToken : Boolean = true;

    if(this.whiteList){
      this.whiteList.forEach(element => {
        if(req.url.includes(`${element.word}`)){
          isNeedToken = false;

          req.clone({
            setHeaders: {
              'Content-Type': 'application/json',
              'Access-Control-Allow-Origin': '*'
            }
          });
        } 
        return next.handle(req);  
      });
    }

    
    if (this.token && isNeedToken) {
      req = this.addTokenHeader(req, this.token.token.accessToken as string);
    }

    if(this.token && req.url.includes(`${this.refreshTokenURL}`)){
      req = this.addTokenHeader(req, this.token.token.refreshToken as string);
    }

    return next.handle(req).pipe(catchError(
      e => {
        // Checamos si tiene acceso...
        if (e.status == 401) {
          if(!e.url.includes(this.accessTokenURL)
              && !e.url.includes(this.refreshTokenURL)) {
            return this.handle401Error(req, next);
          }else {
            // No tiene autorizacion (UnAuthorized)
            // Si se vence el token en el backend..se cierra la sesion en el frontend
            if (this.authService.isAuthenticated()) {
                this.authService.logOut();
            }
            this.router.navigate(['auth/login']);
          }
        }

        // checamos si no esta autorizado...
        if (e.status == 403) {
          this.authService.logOut();
          this.router.navigate(['auth/login']);
          Swal.fire('Acceso Denegado', `Hola ${this.token.userDetails.name} no tienes acceso a este recurso`, 'warning');
        }
        return throwError(e);
      }
    ));
  }

  private isRefreshing = false;
  private refreshTokenSubject: BehaviorSubject<any> = new BehaviorSubject<any>(null);

  private handle401Error(request: HttpRequest<any>, next: HttpHandler) {
    if (!this.isRefreshing) {
      this.isRefreshing = true;
      this.refreshTokenSubject.next(null);

      return this.auth.refreshToken().pipe(
        switchMap((token: any) => {
          this.isRefreshing = false;

          let decodeAcsessToken: TokenPayloadDTO = this.encrDecr.getDecodedAccessToken(token.content.token.accessToken);
          decodeAcsessToken.DanoneTokenPayload.token = token.content.token;
          this.tokenPorviderService.setToken(this._authMapperService.transform(decodeAcsessToken.DanoneTokenPayload));

          this.refreshTokenSubject.next(decodeAcsessToken.DanoneTokenPayload.token.accessToken);
          
          return next.handle(this.addTokenHeader(request, decodeAcsessToken.DanoneTokenPayload.token.accessToken));
        }), catchError((err) => {
          this.isRefreshing = false;
          
          if (this.authService.isAuthenticated()) {
              this.authService.logOut();
          }
          this.router.navigate(['auth/login']);
          return throwError(err);
        })
      );
    }

    return this.refreshTokenSubject.pipe(
      filter(token => token !== null),
      take(1),
      switchMap((token) => next.handle(this.addTokenHeader(request, token)))
    );
  }

  private addTokenHeader(request: HttpRequest<any>, token: string) {
    /* this.token = this.tokenPorviderService.getToken(); */

    return request.clone({
      setHeaders: {
        authorization: `Bearer ${ token }`
      }
    });
  }
}
