import { Inject, Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';

import { AuthMapperService } from "./authMapper.service";
import { EncrDecrService } from './encrDecr.service';
import { TokenPorviderService } from '../interfaces/token-payload-provider.interface';

import { ResponseApiDTO } from '../interfaces/response-api.interface';
import { TokenPayloadDTO } from "../interfaces/token-payload-interface";
import { ILoginProvider } from "../interfaces/login-provider-interface";
import { CONFIGLIBRARY, IConfigLibrary } from "../../../config-library.interface";

import { TokenProviderModel } from "../models/token-payload.model";
import { LoginUserModel } from '../models/login-user.model';
import { ResetPasswordModel } from '../models/reset-password.model';

@Injectable({ providedIn: 'root' })
export class AuthenticationService<UserDetailsType = any, UserRolesType = any> implements ILoginProvider<UserDetailsType,UserRolesType>{
    apiURL:string;
    refreshTokenURL: string;
    accessTokenURL: string;
    resetPasswordURL: string;
    loginType: string = '';

    constructor(
        @Inject(CONFIGLIBRARY) configLibrary: IConfigLibrary,
        private http: HttpClient,
        private encrDecr: EncrDecrService,
        private _authMapperService: AuthMapperService,
        private tokenPorviderService: TokenPorviderService<UserDetailsType,UserRolesType>) {
            this.apiURL = configLibrary.apiURL;
            this.refreshTokenURL = configLibrary.refreshTokenURL;
            this.accessTokenURL = configLibrary.accessTokenURL;
            this.resetPasswordURL = configLibrary.resetPasswordURL;
            this.loginType = configLibrary.loginType;
    }

    public login(user: LoginUserModel): Observable<TokenProviderModel<UserDetailsType,UserRolesType>>{
        let data = this.encrDecr.set(user.userName + '|' + user.password);
        data = data.replace('+', 'xMl3Jk').replace(/\//g, 'Por21Ld').replace('=', 'Ml32');
        let token = {
            'token': data
        }

        let url = `${ this.apiURL }${ this.accessTokenURL }`;
        
        return this.http.post(url, token)
        .pipe(
            map(
                (respApi: any) => {
                    let decodeAcsessToken: TokenPayloadDTO = this.encrDecr.getDecodedAccessToken(respApi.content.token.accessToken);
                    decodeAcsessToken.DanoneTokenPayload.token = respApi.content.token;
                    return this._authMapperService.transform(decodeAcsessToken.DanoneTokenPayload);
                }
            )
        );
    }
    public refreshToken() {
        let url = `${ this.apiURL }${ this.refreshTokenURL }`;

        return this.http.post(url, '');
      }

    public isAuthenticated(): boolean {
        return (this.tokenPorviderService.getToken() ? true : false);
    }

    public logOut(){
        this.tokenPorviderService.removeToken();
    }

    public resetPassword(email: ResetPasswordModel){
        let url = `${ this.apiURL }${ this.resetPasswordURL }`;
        return this.http.post(url, email);
    }
    
}