import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';

import { AuthenticationService } from './services/auth.service';
import { TokenPorviderService } from './interfaces/token-payload-provider.interface';
import { MsalService } from '@azure/msal-angular';
import { AuthenticationResult } from '@azure/msal-browser';
import { AuthAdService } from './services/auth-AD.service';
import { CONFIGLIBRARY, IConfigLibrary } from '../../config-library.interface';
@Component({
   selector: 'lib-security-library-login',
   templateUrl: './login-library.component.html',
   styleUrls: ['./login-library.component.scss']
})
export class LoginLibraryComponent implements OnInit {
   @Output() loginClick = new EventEmitter();
   @Output() resetClick = new EventEmitter();

   public loginForm!: FormGroup;
   public isAuthenticated = false;
   public typeLogin: string = '';
   isAd: boolean = false;
   tokenAd: string = '';

   constructor(
      @Inject(CONFIGLIBRARY) configLibrary: IConfigLibrary,
      private fb: FormBuilder,
      private authService: AuthenticationService,
      private authServiceAD: AuthAdService,
      private tokenPorviderService: TokenPorviderService,
      private msalService: MsalService,
   ) {
      this.typeLogin = configLibrary.loginType;
    }

   public ngOnInit(): void {
      this.loadForm();
      this.typeLogin = this.authService.loginType;
   }

   private loadForm() {
      this.loginForm = this.fb.group({
         userName: new FormControl(null, [Validators.required, Validators.maxLength(50)]),
         password: new FormControl(null, [Validators.required, Validators.maxLength(50)]),
      })
   }

   public login() {
      if (this.loginForm.invalid) {
         this.loginClick.emit(false)
      }
      else {
         this.authUser();
      }
   }

   private authUser() {
      this.authService.login(this.loginForm.value).subscribe(
         response => {
            if (response) {
               this.tokenPorviderService.setToken(response);
               this.loginClick.emit(true);
            }
            else {
               this.loginClick.emit(false)
            }
         },
         error => {
            console.log(error)
            this.loginClick.emit(false)
         }
      )
   }

   public loginAD() {
      this.isAd = true;
      this.msalService.loginPopup().subscribe((response: AuthenticationResult) => {
         this.msalService.instance.setActiveAccount(response.account);
         this.tokenAd = response.accessToken;
         this.authUserAD(this.tokenAd);
      }, error => {
         console.log('Error: ', error);
         this.loginClick.emit(false);
      })
   }

   private authUserAD(token: string) {
      this.authServiceAD.loginAD(token).subscribe(
         response => {
            if (response) {
               this.tokenPorviderService.setToken(response);
               this.loginClick.emit(true);
            }
            else {
               this.loginClick.emit(false)
            }
         },
         error => {
            console.log(error)
            this.loginClick.emit(false)
         }
      )
   }

   public recoveryPassEmit(){
      this.resetClick.emit(true);
   }

}
