import { HttpClient } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { CONFIGLIBRARY, IConfigLibrary } from '../../../config-library.interface';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { ILoginProviderAD } from '../interfaces/login-provider-interface';
import { ResponseApiDTO } from '../interfaces/response-api.interface';
import { TokenPayloadDTO } from '../interfaces/token-payload-interface';
import { TokenProviderModel } from '../models/token-payload.model';
import { AuthMapperService } from './authMapper.service';
import { EncrDecrService } from './encrDecr.service';

@Injectable({
  providedIn: 'root'
})
export class AuthAdService<UserDetailsType = any, UserRolesType = any> implements ILoginProviderAD<UserDetailsType,UserRolesType> {
  apiURL:string;
  refreshTokenURL?: string;
  accessTokenURLAD: string;
  typeLogin?: string;

  constructor(
    @Inject(CONFIGLIBRARY) configLibrary: IConfigLibrary,
    private http: HttpClient,
    private encrDecr: EncrDecrService,
    private _authMapperService: AuthMapperService
  ) { 
    this.apiURL = configLibrary.apiURL;
    this.accessTokenURLAD = configLibrary.accessTokenADURL;
  }

  
  public loginAD(token: string): Observable<TokenProviderModel<UserDetailsType,UserRolesType>>{
    let body = {
      'token': token
    }
    let url = `${ this.apiURL }${ this.accessTokenURLAD }`;
    return this.http.post(url, body)
    .pipe(
        map(
            (respApi: any) => {
                let decodeAcsessToken: TokenPayloadDTO = this.encrDecr.getDecodedAccessToken(respApi.content.token.accessToken);
                decodeAcsessToken.DanoneTokenPayload.token = respApi.content.token;
                return this._authMapperService.transform(decodeAcsessToken.DanoneTokenPayload);
            }
        )
    );
}

}
