import { InjectionToken } from '@angular/core';

export class IConfigLibrary{
    whiteList!: IWhiteList[];
    encryptPhrase!:string;
    apiURL!:string;
    refreshTokenURL!:string;
    accessTokenURL!:string;
    accessTokenADURL!:string;
    resetPasswordURL!:string;
    loginType!:string;
    clientId!:string;
    authority!:string;
    redirectUri!:string;
}

export class IWhiteList{
    word!: string
}


export const defaultConfigLibrary: IConfigLibrary = {
    whiteList: [
        {
            word: 'oauth/token',
        }
    ],
    encryptPhrase: 'dAN0n3IR0nM4n4p1',
    apiURL: 'http://localhost:8082/',
    refreshTokenURL: 'oauth/refreshToken',
    accessTokenURL: 'oauth/token',
    accessTokenADURL: 'oauth/tokenAd',
    resetPasswordURL: 'oauth/resetPassword',
    loginType: 'both',
    clientId: '',
    authority: '',
    redirectUri: '',
}

export const CONFIGLIBRARY = new InjectionToken<IConfigLibrary>('defaultConfigLibrary');
