export class UserRolesModel{
    id:number | undefined;
    name: string | undefined;
    functions: FunctionModel[] | undefined;
    companies: CompanyModel[] | undefined;
    paymentPeriodicity: PaymentPeriodicityModel[] | undefined;
    rfcExcluded: RFCExcludedModel[] | undefined;
}

export class FunctionModel{
    id: number | undefined;
    name: string | undefined;
    action: string | undefined;
}

export class CompanyModel{
    id: number | undefined;
    cbu: string | undefined;
}

export class PaymentPeriodicityModel{
    id!: number;
    code!: string;
}

export class RFCExcludedModel{
    rfcReceptor!: string;
}