import { Injectable } from '@angular/core';

import { TokenProviderDTO } from "../interfaces/token-payload-interface";
import { TokenProviderModel } from "../models/token-payload.model";
import { MapperService } from './mapperTemplate.service';

@Injectable({ providedIn: 'root' })
export class AuthMapperService extends MapperService<TokenProviderDTO<any, any>, TokenProviderModel<any,any>> {

    protected map(response: TokenProviderDTO<any,any>): TokenProviderModel<any,any> {
        return {
            userDetails: (response as unknown as TokenProviderModel<any, any>).userDetails,
            roles: (response as unknown as TokenProviderModel<any, any>).roles,
            token: (response as unknown as TokenProviderModel<any, any>).token
        }
    } 
}