import { Inject, Injectable } from '@angular/core';

import * as CryptoJS from 'crypto-js';
import jwt_decode from 'jwt-decode';

import { CONFIGLIBRARY, IConfigLibrary } from "../../../config-library.interface";

@Injectable({
    providedIn: 'root'
})

export class EncrDecrService {  
    encryptPhrase:string

    constructor(
        @Inject(CONFIGLIBRARY) configLibrary: IConfigLibrary
    ){
        this.encryptPhrase = configLibrary.encryptPhrase 
    }
    //Encrypt
    set(value: any) {
        var key = CryptoJS.enc.Utf8.parse(this.encryptPhrase);
        var iv = CryptoJS.enc.Utf8.parse(this.encryptPhrase);
        var encrypted = CryptoJS.AES.encrypt(CryptoJS.enc.Utf8.parse(value.toString()), key,
            {
            //    keySize: 128 / 8,
                iv: iv,
                mode: CryptoJS.mode.CBC,
                padding: CryptoJS.pad.ZeroPadding
            });

        return encrypted.toString();
    }

    //Decrypt
    get(value: any) {
        var key = CryptoJS.enc.Utf8.parse(this.encryptPhrase);
        var iv = CryptoJS.enc.Utf8.parse(this.encryptPhrase);
        var decrypted = CryptoJS.AES.decrypt(value, key, {
           // keySize: 128 / 8,
            iv: iv,
            mode: CryptoJS.mode.CBC,
            padding: CryptoJS.pad.ZeroPadding
        });

        return decrypted.toString(CryptoJS.enc.Utf8);
    }

    getDecodedAccessToken(token: string): any {
        try{
            return jwt_decode(token);
        }
        catch(Error){
            return null;
        }
      }
}